<script>
import {Table, TableColumn} from 'element-ui'
import axios from "axios";
import {baseUrl} from "../../../config/index.json";

export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      selected: [], // Array reference
      options: [
      ],
      is_negative:false,
      is_positive:false,
      selected1: [], // Array reference
      options1: [
      ],
      imageUrl:[],
      myImages:[],
      files_name:[],
      imageUrl1:[],
      myImages1:[],
      files_name1:[],
      imageUrl2:[],
      myImages2:[],
      files_name2:[],
      profession_aspects:'',
      profession_name:'',
      profession_type:'',
      min_salary:'',
      mid_salary:'',
      max_salary:'',
      workplace:'',
      working_condition:'',
      subject:'',
      practice:'',
      higher_education_info:'',
      about:'',
      title:'',
      //ru
      profession_aspectsru:'',
      profession_nameru:'',
      profession_typeru:'',
      min_salaryru:'',
      mid_salaryru:'',
      max_salaryru:'',
      workplaceru:'',
      working_conditionru:'',
      subjectru:'',
      practiceru:'',
      higher_education_inforu:'',
      aboutru:'',
      titleru:'',
      order:'',
      //en
      profession_aspectsen:'',
      profession_nameen:'',
      profession_typeen:'',
      min_salaryen:'',
      mid_salaryen:'',
      max_salaryen:'',
      workplaceen:'',
      working_conditionen:'',
      subjecten:'',
      practiceen:'',
      higher_education_infoen:'',
      abouten:'',
      titleen:''
    };
  },
  methods: {
    trySubmitFile(e) {
      this.imageUrl=[]
      this.myImages=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile1(e) {
      this.imageUrl1=[]
      this.myImages1=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile2(e) {
      this.imageUrl2=[]
      this.myImages2=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name2.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages2.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl2.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    createOz(){
      const form = new FormData();
      axios.put(`${baseUrl}aspects/${this.$route.params.id}/`,{
          "content":this.title,
          "profession":this.selected1,
          "is_positive":this.is_positive,
          "is_negative":this.is_negative
        },
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'uz'
          }
        })
        .then(res=>{
          this.$toast("Ma'lumotlar O'zbek tillarda kiritildi", {
            timeout: 2000,
            type:"success"
          });
          axios.put(`${baseUrl}aspects/${res.data.id}/`,{
              "content":this.titleru,
              "profession":this.selected1
            },
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language':'ru'
              }
            })
            .then(()=>{
              this.$toast("Ma'lumotlar rus tilida kiritildi", {
                timeout: 2000,
                type:"success"
              });
              //en

              form.append("title", this.titleen);
              axios.put(`${baseUrl}aspects/${res.data.id}/`,{
                  "content":this.titleen,
                  "profession":this.selected1
                },
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'kar'
                  }
                })
                .then(()=>{
                  this.$toast("Ma'lumotlar barcha tillarda kiritildi", {
                    timeout: 2000,
                    type:"success"
                  });
                })
            })
          this.$router.push(`/profession/${this.selected1}/aspects`)
        })
    },

  },
  mounted() {
    axios.get(`${baseUrl}subjects/`)
      .then(res=>{
        res.data.forEach(el=>{
          console.log(el)
          this.options.push({
            value:el.id,
            text: el.title
          })
        })

      })
    axios.get(`${baseUrl}professions/`)
      .then(res=>{
        res.data.forEach(el=>{
          console.log(el)
          this.options1.push({
            value:el.id,
            text: el.title
          })
        })

      })
    axios.get(`${baseUrl}aspects/${this.$route.params.id}`,{
      headers:{
        'Accept-Language':'oz'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.title=this.menus.content
        this.is_positive=this.menus.is_positive
        this.is_negative=this.menus.is_negative
        this.selected1=this.menus.profession
      })
    axios.get(`${baseUrl}aspects/${this.$route.params.id}`,{
      headers:{
        'Accept-Language':'ru'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.titleru=this.menus.content

